import React, {useCallback, useMemo} from 'react';
import Page from '../../components/Page';
import {createGlobalStyle} from 'styled-components';
import {Route, Switch, useRouteMatch} from 'react-router-dom';
import {useWallet} from 'use-wallet';
import UnlockWallet from '../../components/UnlockWallet';
import PageHeader from '../../components/PageHeader';
import ExchangeCard from './components/ExchangeCard';
import styled from 'styled-components';
import Spacer from '../../components/Spacer';
import useBondStats from '../../hooks/useBondStats';
//import useBnbmStats from '../../hooks/useBnbmStats';
import useBnbmFinance from '../../hooks/useBnbmFinance';
import useCashPriceInLastTWAP from '../../hooks/useCashPriceInLastTWAP';
import {useTransactionAdder} from '../../state/transactions/hooks';
import ExchangeStat from './components/ExchangeStat';
import useTokenBalance from '../../hooks/useTokenBalance';
import useBondsPurchasable from '../../hooks/useBondsPurchasable';
import {getDisplayBalance} from '../../utils/formatBalance';
import { BOND_REDEEM_PRICE, BOND_REDEEM_PRICE_BN } from '../../bnbm-finance/constants';
import { Alert } from '@material-ui/lab';
import { Grid, Box } from '@material-ui/core';
import { Helmet } from 'react-helmet';

import {BackgroundImage} from '../Home/Home.js';
const TITLE = 'bnbm.finance | Bonds'

const Bond: React.FC = () => {
  const {path} = useRouteMatch();
  const {account} = useWallet();
  const bnbmFinance = useBnbmFinance();
  const addTransaction = useTransactionAdder();
  const bondStat = useBondStats();
  //const bnbmStat = useBnbmStats();
  const cashPrice = useCashPriceInLastTWAP();

  const bondsPurchasable = useBondsPurchasable();

  const bondBalance = useTokenBalance(bnbmFinance?.BBNB);
  //const scalingFactor = useMemo(() => (cashPrice ? Number(cashPrice) : null), [cashPrice]);
  
  const handleBuyBonds = useCallback(
    async (amount: string) => {
      const tx = await bnbmFinance.buyBonds(amount);
      addTransaction(tx, {
        summary: `Buy ${Number(amount).toFixed(2)} BBNB with ${amount} BNBM`,
      });
    },
    [bnbmFinance, addTransaction],
  );

  const handleRedeemBonds = useCallback(
    async (amount: string) => {
      const tx = await bnbmFinance.redeemBonds(amount);
      addTransaction(tx, {summary: `Redeem ${amount} BBNB`});
    },
    [bnbmFinance, addTransaction],
  );
  const isBondRedeemable = useMemo(() => cashPrice.gt(BOND_REDEEM_PRICE_BN), [cashPrice]);
  const isBondPurchasable = useMemo(() => Number(bondStat?.tokenInFtm) < 1.01, [bondStat]);
  const isBondPayingPremium = useMemo(() => Number(bondStat?.tokenInFtm) >= 1.1, [bondStat]);
  const bondScale = (Number(cashPrice) / 10000000000000000).toFixed(2); 

  return (
    <Switch>
      <Page>
        <BackgroundImage />
              <Helmet>
        <title>{TITLE}</title>
      </Helmet>
        {!!account ? (
          <>
            <Route exact path={path} >
              <h2 style={{color:'white', textAlign:'center'}}>Buy &amp; Redeem Bonds</h2>
              <p style={{color:'white', textAlign:'center'}}>Earn premiums upon redemption</p>
            </Route>
            


              <Box mt={5} style={{marginTop:'30px'}}>
            {isBondPayingPremium === false ? (  
                <Grid item xs={12} sm={12} justify="center" style={{ margin: '18px', display: 'flex' }}>
                <Alert style={{fontSize : '16px', fontFamily:'eurostyleextended'}} variant="filled" severity="error">
                    <b>
                      Claiming below 1.1 peg will not receive a redemption bonus, claim wisely!</b>
              </Alert>
            
              </Grid>
              ) : <></>}
              </Box>
            
          
            <StyledBond>
              <StyledCardWrapper>
                <ExchangeCard
                  action="Purchase"
                  fromToken={bnbmFinance.BNBM}
                  fromTokenName="BNBM"
                  toToken={bnbmFinance.BBNB}
                  toTokenName="BBNB"
                  priceDesc={
                    !isBondPurchasable
                      ? 'BNBM is over peg'
                      : getDisplayBalance(bondsPurchasable, 18, 2) + ' BBNB available for purchase'
                  }
                  onExchange={handleBuyBonds}
                  disabled={!bondStat || isBondRedeemable}
                />
              </StyledCardWrapper>
              
              <StyledCardWrapper>
                <ExchangeCard
                  action="Redeem"
                  fromToken={bnbmFinance.BBNB}
                  fromTokenName="BBNB"
                  toToken={bnbmFinance.BNBM}
                  toTokenName="BNBM"
                  priceDesc={`${getDisplayBalance(bondBalance)} BBNB Available in wallet`}
                  onExchange={handleRedeemBonds}
                  disabled={!bondStat || bondBalance.eq(0) || !isBondRedeemable}
                  disabledDescription={!isBondRedeemable ? `Enabled when 100 BNBM > ${BOND_REDEEM_PRICE}BNB` : null}
                />
              </StyledCardWrapper>
            </StyledBond>
            <StyledStatsWrapper>
                <ExchangeStat
                  tokenName="100 BNBM"
                  description="Last-Hour TWAP Price"
                  //price={Number(bnbmStat?.tokenInFtm).toFixed(4) || '-'}
                 price={bondScale || '-'}

                />
                <Spacer size="md" />
                <ExchangeStat
                  tokenName="100 BBNB"
                  description="Current Price: (BNBM)^2"
                  price={Number(bondStat?.tokenInFtm).toFixed(4) || '-'}
                />
              </StyledStatsWrapper>
          </>
        ) : (
          <UnlockWallet />
        )}
      </Page>
    </Switch>
  );
};

const StyledBond = styled.div`
  display: flex;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`;

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 80%;
  };
  margin-right: 40px;
  margin-left: 40px;
  
`;

const StyledStatsWrapper = styled.div`
  display: flex;
  flex: 0.8;
  margin: 0 20px;
  flex-direction: column;

  @media (max-width: 768px) {
    width: 80%;
    margin: 16px 0;
  };
  margin-top: 40px
`;

export default Bond;
