import {useEffect, useState} from 'react';
import {BigNumber} from 'ethers';
//import ERC20 from '../bnbm-finance/ERC20';
import useBnbmFinance from './useBnbmFinance';
//import config from '../config';

const useBondsPurchasable = () => {
  const [balance, setBalance] = useState(BigNumber.from(0));
  const bnbmFinance = useBnbmFinance();

  useEffect(() => {
    async function fetchBondsPurchasable() {
      try {
        setBalance(await bnbmFinance.getBondsPurchasable());
      } catch (err) {
        console.error(err);
      }
    }
    fetchBondsPurchasable();
  }, [setBalance, bnbmFinance]);

  return balance;
};

export default useBondsPurchasable;
