import {useCallback, useEffect, useState} from 'react';
import useBnbmFinance from './useBnbmFinance';
import config from '../config';
import {BigNumber} from 'ethers';

const useCashPriceInLastTWAP = () => {
  const [price, setPrice] = useState<BigNumber>(BigNumber.from(0));
  const bnbmFinance = useBnbmFinance();

  const fetchCashPrice = useCallback(async () => {
    setPrice(await bnbmFinance.getBnbmPriceInLastTWAP());
  }, [bnbmFinance]);

  useEffect(() => {
    fetchCashPrice().catch((err) => console.error(`Failed to fetch BNBM price: ${err.stack}`));
    const refreshInterval = setInterval(fetchCashPrice, config.refreshInterval);
    return () => clearInterval(refreshInterval);
  }, [setPrice, bnbmFinance, fetchCashPrice]);

  return price;
};

export default useCashPriceInLastTWAP;
